export default "baseUnit\n<template>\n  <section class=\"unitTitle\">\n    <span class=\"titIcon\"></span>\n    <div class=\"theTitle\">{{ title }}</div>\n    <div class=\"rightTip\">\n      <slot></slot>\n    </div>\n  </section>\n</template>\n\n<script>\n  export default {\n    props: {\n      title: {\n        type: String,\n        default: '标题'\n      }\n    },\n    data() {\n      return {};\n    }\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .unitTitle {\n    position: relative;\n    display: flex;\n    align-items: center;\n    height: 36px;\n    line-height: 36px;\n    font-size: 20px;\n    padding-left: 10px;\n    background: linear-gradient(to right, #081e4e, #021637 40%);\n    color: #22aeff;\n    &:before,\n    &:after {\n      content: '';\n      position: absolute;\n      right: 0;\n      top: 0;\n      width: 5px;\n      height: 2px;\n      background-color: #06c3de;\n    }\n    &:before {\n      right: 8px;\n    }\n    .titIcon {\n      position: relative;\n      display: inline-block;\n      width: 16px;\n      height: 16px;\n      margin-right: 8px;\n      box-sizing: border-box;\n      border: 1px solid #00dbf7;\n      &:before {\n        content: '';\n        position: absolute;\n        left: 5px;\n        top: 5px;\n        width: 4px;\n        height: 4px;\n        background-color: #fcd641;\n      }\n    }\n    .theTitle {\n    }\n    .rightTip {\n    }\n  }\n</style>\n";